import React from 'react';
import { connect } from 'react-redux';

// Actions
import { startHandshake } from '../actions/startHandshakeActions'

// Components
import Spinner from 'react-spin';

/**
 * Component for dispatching the initial web auth flow state.
 */
@connect((store) => {
    return {
        operationId: store.operationId
    }
})
export default class StartHandshake extends React.Component {

    componentWillMount() {
        this.props.dispatch(startHandshake());
    }

    render() {
        return (
            <div id="center">
                <Spinner/>
            </div>
        )
    }

}


